.footer {
    background: #000848;
    padding: 110px 0 180px 0;
    margin-top: 120px;
    color: #fff;
    position: relative;
    &-container {
        display: flex;
        max-width: 1200px;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 auto;
    }
    &-logo {
        &-blocks {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            width: 100%;
            margin-top: 25px;
        }
        &-block {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: rgba(255, 255, 255, 0.29);
            display: grid;
            align-items: center;
            justify-items: center;
            margin: 0 7px;
        }
    }
    &-block {
        &-title {
            font-weight: 500;
            font-size: 20px;
            line-height: 160%;
            color: #ffffff;
        }
        &-list {
            padding: 0;
            li {
                margin: 10px 0;
                a {
                    color: #ffffff;
                    opacity: 0.7;
                    text-decoration: none;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 160%;
                }
            }
        }
    }
    &-decor {
        position: absolute;
        &-1 {
            background: linear-gradient(270.78deg, #00053c -21.36%, #010c64 100.6%);
            transform: matrix(-1, 0, 0, 1, 0, 0) rotate(10deg);
            bottom: -75px;
            right: -15px;
            width: 950px;
            height: 114px;
            z-index: 2;
        }
        &-2 {
            background: linear-gradient(90.21deg, #000848 0.27%, #1f0977 106.71%);
            width: 950px;
            height: 114px;
            left: -15px;
            bottom: -75px;
            transform: rotate(10deg);
            z-index: 2;
        }
        &-3 {
            background: linear-gradient(271.92deg, #050b42 3.69%, #05106b 116.09%);
            width: 1315px;
            height: 187px;
            left: -40px;
            bottom: -100px;
            transform: rotate(10deg);
            z-index: 1;
        }
        &-4 {
            background: linear-gradient(270.77deg, #000423 -21.37%, #050a59 100.6%);
            transform: matrix(-1, 0, 0, 1, 0, 0);
            width: 1300px;
            height: 125px;
            bottom: 0;
            right: 0;
        }
    }
}
