.box_circle_progress {
    svg {
        width: 70px;
        height: 70px;
        z-index: 10;
        position: relative;
        > circle {
            width: 100%;
            height: 100%;
            fill: none;
            stroke: rgb(209, 206, 206);
            stroke-width: 5;
            stroke-linecap: round;
            &:nth-child(2) {
                stroke-dasharray: 180;
                // stroke-dashoffset: 360;
                transform-origin: center;
                // transform: rotate(-90deg);
                // stroke-dashoffset: calc(180 - (180 * attr(data-perc)) / 100);
                stroke: rgb(189, 117, 196);
            }
        }
    }
    .percent {
        position: relative;
        width: 70px;
        height: 70px;
        .number {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            h3 {
                font-size: 16px;
                margin: 0;
            }
        }
    }
}
