@media all and (max-width: 420px) {
    .news-block-text {
        font-size: 25px;
    }
    .news-button {
        top: 100%;
        left: 0;
        width: 100%;
    }
    .news-inp-block {
        width: 90%;
        margin: 35px auto 0;
    }
    .users-block-dots {
        width: 80px;
        height: 80px;
        svg {
            width: 50%;
            height: 50%;
        }
    }
    .users-title {
        font-size: 30px;
    }
    .users-block-left-title {
        font-size: 25px;
    }
    .users-name {
        font-size: 20px;
    }
    .users-bio {
        font-size: 16px;
    }
    .header-center {
        padding-left: 40px;
        height: 110%;
    }
    .simple-left-bottom {
        width: 100%;
        padding: 0;
    }
    .peoples {
        width: 100%;
        &-block {
            margin: 5px 15px;
        }
    }
    .simple-right {
        margin-top: 150px;
    }
    .form {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        height: auto;
        margin-top: 0;
        input,
        button {
            width: 70%;
            margin: 10px 0;
        }
    }
    .simple-left {
        align-items: stretch;
    }
    .title-black {
        font-size: 30px;
    }
    .accepts p span {
        font-size: 16px;
    }
    .committed-hover {
        padding-right: 20px;
        padding-left: 20px;
    }
    .choose {
        &-slide {
            margin: 0 10px;
        }
        &-icon {
            left: 7%;
        }
        &-change {
            grid-template-columns: repeat(1, 1fr);
            label {
                margin: 20px auto 0 !important;
            }
        }
    }
}
