.header {
	position: absolute;
	top: 60px;
	width: 100%;
	z-index: 2;
	&-container {
		max-width: 1300px;
		margin: 0 auto;
		align-items: center;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}
	&-center {
		display: flex;
		width: 500px;
		padding: 0;
		justify-content: space-between;
		a {
			font-weight: 500;
			font-size: 20px;
			line-height: 160%;
			color: #ffffff;
			opacity: 0.7;
		}
	}
	&-right {
		justify-self: center;
	}
	&-active {
		opacity: 1 !important;
	}
}
.button {
	width: 172px;
	height: 62px;
	display: grid;
	text-align: center;
	align-items: center;
	border-radius: 4px;
	font-weight: 500;
	font-size: 20px;
	line-height: 160%;
	color: #ffffff;
	&-orange {
		background: #ff5a05;
	}
	&-border {
		border: 1px solid #fff;
		opacity: 0.9;
	}
	&-choose {
		background: rgba(255, 90, 5, 0.1);
		color: #ff5a05;
	}
	&-pack {
		width: 335px;
		margin: 50px auto;
	}
}
