@media all and (min-width: 1000px) and (max-width: 1250px) {
}
@media all and (max-width: 1250px) {
    .buttons {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
        .button {
            margin: 10px 0;
        }
    }
    .footer-container {
        padding: 0 25px;
    }
    .main-content-container {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
    }
    .main-content {
        height: auto;
        &-left {
            order: 1;
            text-align: center;
            margin-top: 50px !important;
            > p {
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
    .after {
        &-line::after {
            width: 100%;
        }
    }
    .main_title {
        width: 90%;
    }
    .buttons {
        margin: 0 auto;
        width: 50%;
    }
    .simple {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        &-left {
            height: 456px;
            width: 546px;
        }
        &-right {
            margin-top: 50px;
            justify-self: center;
        }
    }
}
