@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 600px) {
    .header-center {
        width: 90%;
    }
    .footer-container {
        justify-content: center;
    }
    .footer-block {
        margin: 30px;
    }
    .footer-decor {
        &-1 {
            bottom: -40px;
        }
    }
}
@media all and (max-width: 650px) {
    .simple-left {
        width: 100%;
    }
    .users-block {
        &-right {
            width: 90%;
            img {
                width: 100%;
            }
        }
        &-left {
            width: 90%;
            &-block {
                width: 100%;
                grid-template-columns: repeat(1, 1fr);
            }
            &-info {
                width: 90%;
                box-sizing: border-box;
            }
        }
        &-text {
            grid-column: 1/2;
            width: 100%;
        }
    }
}
@media all and (max-width: 750px) {
    .simple {
        &-left {
            height: 100%;
            display: grid;
            img {
            }
            &-top,
            &-bottom {
                position: static;
                margin: 20px auto;
            }
        }
    }
}
@media all and (max-width: 500px) {
    .simple-left {
        align-items: center;
        justify-items: center;
        img {
            position: static;
            width: 100%;
        }
        &-top {
            margin: 20px 0;
        }
        &-bottom {
            padding: 0;
        }
    }
    .choose {
        &-slide {
            margin: 0 30px;
        }
        &-icon {
            left: 15%;
        }
    }
    .header-center {
        li {
            height: max-content;
        }
    }
    .burger-click {
        margin-right: 20px !important;
        width: 40px;
        &::after {
            width: 40px;
        }
        &::before {
            width: 40px;
        }
    }
    .buttons {
        width: 100%;
    }
    .main_title {
        font-size: 25px;
        &::first-line {
            font-size: 40px;
        }
    }
}
