// Подключение своих SCSS файлов
@import "fonts";

.wrapper {
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}
body {
    font-family: "Inter";
}
a,
button {
    border: none;
    text-decoration: none;
    cursor: pointer;
    background: none;
}
ul {
    list-style: none;
}
.main-content {
    height: 820px;
    background: linear-gradient(264.47deg, #2e43f0 29.39%, #6677ff 93.49%);
    color: #ffffff;
    position: relative;
    overflow: hidden;
    &-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        max-width: 1300px;
        position: relative;
        margin: 0 auto;
        > div {
            margin-top: 180px;
            z-index: 2;
        }
    }
}
.text {
    &-1 {
        font-weight: 600;
        font-size: 20px;
        line-height: 160%;
        letter-spacing: 0.1em;
        text-transform: uppercase;
    }
    &-2 {
        font-weight: 500;
        font-size: 16px;
        line-height: 160%;
        color: #ffffff;
        opacity: 0.7;
        max-width: 418px;
    }
}
.main_title::first-line {
    font-weight: bold;
    font-size: 61px;
}
.main_title {
    font-weight: 500;
    line-height: 120%;
    font-size: 50px;
}
.after {
    position: relative;
    &-line::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -20px;
        left: 0;
        height: 1px;
        width: 80%;
        background: #fff;
    }
    &-arrow {
    }
    &-bottom::after {
        content: "";
        display: block;
        width: 185px;
        height: 185px;
        background: #ffffff;
        box-shadow: 10px 15px 25px rgba(0, 0, 0, 0.15);
        transform-origin: center;
        transform: rotate(45deg);
        z-index: -11;
        position: absolute;
        top: 61%;
        left: 15%;
    }
}
.buttons {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 90%;
}
.shadow {
    background: linear-gradient(267.65deg, rgba(69, 129, 247, 0.61) 20.33%, rgba(69, 129, 247, 0) 89.01%);
    transform: rotate(-12.22deg);
    width: 980px;
    height: 315px;
    position: absolute;
    right: -10%;
    top: 50%;
    z-index: 1;
}
.big_shadow {
    background: linear-gradient(268.07deg, #001dfa 10.47%, #8996ff 104.89%);
    transform: rotate(-10.43deg);
    width: 1840px;
    height: 605px;
    position: absolute;
    left: -10%;
    bottom: 15%;
}
.close {
    display: none;
}
.burger-click {
    display: none;
}
.container-small {
    max-width: 1135px;
    margin: 120px auto 0;
}
.partners {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &-block {
        width: 260px;
        height: 82px;
        margin: 20px 10px;
        background: #ffffff;
        box-shadow: 1px 4px 12px rgba(0, 0, 0, 0.2);
        display: grid;
        justify-items: center;
        align-items: center;
        &-margin {
            &-left {
                // margin-left: 40px;
            }
            &-right {
                // margin-right: 40px;
            }
        }
    }
}
.container-normal {
    width: 1300px;
    margin: 0 auto;
}
.simple {
    margin-top: 120px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    &-right {
        max-width: 546px;
        justify-self: end;
    }
}
.title {
    &-simple {
        font-weight: 600;
        font-size: 17px;
        line-height: 160%;
        text-transform: uppercase;
        color: #ff5a05;
        opacity: 0.8;
        margin: 30px 0 0 0;
    }
    &-black {
        font-weight: bold;
        font-size: 49px;
        line-height: 120%;
        color: #05000b;
        margin: 10px 0 25px;
    }
}
.text {
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    color: #05000b;
    opacity: 0.7;
    margin: 0 0 25px 0;
}
.accepts {
    display: flex;
    justify-content: space-between;
    max-width: 410px;
    margin-bottom: 15px;
    p {
        display: flex;
        align-items: center;
        img {
            margin-right: 15px;
        }
        span {
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 160%;
            color: #05000b;
        }
    }
}
.abs {
    position: absolute;
}
.form {
    button {
        width: 100%;
    }
}
.simple-left {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    img {
        position: static;
        height: 100%;
        width: max-content;
        // left: 0;
        // top: 0;
    }
    &-top {
        position: absolute;
        display: flex;
        right: -5%;
        top: 50%;
        // margin-top: 100px;
        // margin-bottom: -50px;
        justify-content: space-between;
        width: 250px;
        z-index: 2;
        background: #ffffff;
        border-radius: 7.69231px;
        align-items: center;
        height: 60px;
        .box_circle_progress {
        }
    }
    &-bottom {
        right: -10%;
        bottom: -50px;
        position: absolute;
        z-index: 2;
        display: grid;
        background: #ffffff;
        box-shadow: 20px 30px 70px rgba(3, 7, 37, 0.06);
        border-radius: 10px;
        width: 400px;
        height: 200px;
        box-sizing: border-box;
        padding: 20px 30px 30px;
        &-title {
            font-family: "Supreme";
            font-style: normal;
            font-weight: 600;
            font-size: 20.1118px;
            line-height: 25px;
            color: #404040;
            margin: 0 15px;
        }
    }
}
.circle {
    height: 80px;
    width: 80px;
    background: #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    margin-left: 15px;
    justify-content: center;
}
.leg {
    font-family: "Source Sans Pro";
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: rgba(60, 60, 67, 0.6);
}
.procent {
    font-family: SF Pro Text;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 18px;
    color: #7459d9;
}
.peoples {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &-block {
        display: grid;
        align-items: center;
        justify-items: center;
        margin: 0 6px;
        height: 82px;
    }
}
.name {
    font-family: "Suprema";
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #404040;
    &:last-child {
        margin: 0;
    }
}
.plus {
    width: 20px;
    height: 20px;
    background: #ff5a05;
    border-radius: 50%;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
.inp {
    &:focus {
        border: 1px solid #1f37ff !important;
    }
    &:active {
        border: 1px solid #1f37ff !important;
    }
}
.form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 36px;
    margin-top: 16px;
    input,
    button {
        height: 36px;
        box-sizing: border-box;
    }
    input {
        background: #ffffff;
        border: 1px solid #1f37ff !important;
        box-sizing: border-box;
        border-radius: 8px;
        padding: 0 5px;
        font-family: "Lato";
        font-style: normal;
        font-weight: 300;
        font-size: 20.1118px;
        line-height: 24px;
        color: #c7c7c7;
        &::placeholder {
            text-align: end;
            font-family: "Lato";
            font-style: normal;
            font-weight: 300;
            font-size: 20.1118px;
            line-height: 24px;
            color: #c7c7c7;
        }
    }
    &-button {
        font-family: "Quicksand";
        font-size: 11px !important;
        margin-left: 10px;
    }
}
.stats {
    &-blocks {
        display: flex;
        justify-content: center;
        margin-bottom: 100px;
        flex-wrap: wrap;
    }
    &-block {
        width: 260px;
        height: 250px;
        background: #ffffff;
        box-shadow: 10px 0px 25px 0px rgba(0, 0, 0, 0.15);
        padding-top: 100px;
        box-sizing: border-box;
        margin: 30px 10px 120px;
        text-align: center;
        position: relative;
        p {
            z-index: 21;
        }
        &-num {
            font-weight: bold;
            font-size: 55px;
            line-height: 120%;
            text-align: center;
            color: #1f37ff;
            margin: 0;
        }
        &-text {
            font-weight: 600;
            font-size: 20px;
            line-height: 160%;
            color: #05000b;
            opacity: 0.7;
        }
    }
}
.committed {
    background: #fff9f5;
    padding: 85px 0 75px;
    &-blocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &-block {
        margin: 0px 10px 30px;
        width: 358px;
        svg {
            path {
                fill: #1f37ff;
            }
        }
        &-text {
            font-weight: 600;
            font-size: 20px;
            line-height: 160%;
            color: #05000b;
        }
    }
    &-title {
        font-size: 20px;
        line-height: 160%;
    }
    &-text {
        font-weight: 500;
        font-size: 16px;
        line-height: 160%;
        color: #05000b;
        opacity: 0.7;
        &-1 {
            font-weight: bold;
            font-size: 31px;
            line-height: 130%;
            color: #05000b;
            margin: 15px 0 20px;
        }
    }
    &-hover {
        box-sizing: border-box;
        padding: 35px 70px 35px 35px;
        background: #ffffff;
        border: 2px solid rgba(66, 82, 207, 0.1);
        box-sizing: border-box;
        border-radius: 10px;
        cursor: pointer;
        transition: all 0.1s ease-in-out;
        &:hover {
            background: #4252cf;
            svg {
                path {
                    fill: #fff;
                }
            }
            p {
                color: #fff;
            }
        }
    }
}
.users {
    &-title {
        text-align: center;
        font-weight: bold;
        font-size: 49px;
        line-height: 120%;
        color: #05000b;
        margin: 0 0 25px 0;
        &-under {
            font-size: 16px;
            line-height: 160%;
            text-align: center;
            color: #05000b;
            opacity: 0.7;
            text-align: center;
            max-width: 500px;
            margin: 0 auto;
        }
    }
    &-blocks {
        margin-top: 70px;
    }
    &-block {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        &-left {
            position: relative;
            &-title {
                font-weight: 600;
                font-size: 31px;
                line-height: 160%;
                color: #05000b;
            }
            &-block {
                align-items: center;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                width: 655px;
                position: absolute;
                top: 30%;
                background: #fff;
                z-index: 10;
            }
            &-info {
                background: #ffffff;
                box-shadow: 13px 13px 31px rgba(0, 0, 0, 0.05);
                padding: 30px 0 30px 30px;
                width: 373px;
                transform: translateY(-35px);
            }
        }

        &-text {
            grid-column: 1/3;
            max-width: 515px;
            font-style: italic;
            font-weight: normal;
            font-size: 16px;
            line-height: 160%;
            color: #05000b;
        }
        &-right {
            position: relative;
        }
        &-dots {
            position: absolute;
            background: #1f37ff;
            border-radius: 50% 50% 50% 0px;
            right: 0;
            top: 0;
            width: 125px;
            height: 125px;
            display: grid;
            align-items: center;
            justify-items: center;
        }
    }
    &-name {
        font-weight: 600;
        font-size: 25px;
        line-height: 160%;
        color: #05000b;
        margin: 0;
    }
    &-bio {
        font-size: 20px;
        line-height: 160%;
        color: #05000b;
        opacity: 0.8;
        margin: 0;
    }
}
.choose {
    position: relative;
    margin-top: 120px;
    box-sizing: border-box;
    padding-top: 60px;
    &-background {
        background: #ebedff;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 500px;
        z-index: -1;
    }
    &-title {
        font-weight: bold;
        font-size: 31px;
        line-height: 160%;
        text-align: center;
        color: #05000b;
        max-width: 455px;
        margin: 0 auto;
    }
    &-change {
        display: grid;
        margin: 25px auto 45px;
        max-width: 360px;
        grid-template-columns: repeat(2, 1fr);
        background: #ffffff;
        border-radius: 10px;
        padding: 15px 27px;
        input {
            display: none;
        }
        label {
            border-radius: 10px;
        }
        > label:nth-child(2) {
            margin-right: 30px;
        }
        input:checked + label {
            background: #ff5a05;
            color: white;
        }
    }
    &-blocks {
        width: 940px;
        margin: 0 auto;
        > div {
            display: none;
        }
    }
    &-active {
        display: block !important;
        .slick-track {
        }
    }
    &-slide {
        max-width: 455px;
        margin: 0 30px;
        background: #ffffff;
        border: 1px solid rgba(31, 55, 255, 0.1);
        border-radius: 10px; // display: flex;
        position: relative;
        box-sizing: border-box;
        padding-top: 100px;
        p {
            text-align: center;
        }
    }
    &-icon {
        width: 200px;
        height: 150px;
        background: #1f37ff;
        transform-origin: top;
        border-radius: 5px;
        position: absolute;
        transform: rotate(-45deg);
        left: 15%;
        top: -80px;
        display: grid;
        border-bottom-left-radius: 50%;
        svg {
            transform-origin: top;
            transform: rotate(45deg);
            margin: 85px 0 0 40px;
        }
    }
    &-type {
        font-weight: 600;
        font-size: 31px;
        line-height: 160%;
        color: #05000b;
        margin: 0;
    }
    &-price {
        font-weight: bold;
        font-size: 96.3951px;
        line-height: 120%;
        color: #1f37ff;
        margin: 0;
    }
    &-pack {
        font-weight: 500;
        font-size: 20px;
        line-height: 160%;
        color: #05000b;
        margin: 0;
    }
    &-list {
        width: max-content;
        padding: 0;
        margin: 0 auto;
        li {
            margin: 15px 0;
            align-items: center;
            display: flex;
            font-weight: 500;
            font-size: 16px;
            line-height: 160%;
            color: #05000b;
            &::before {
                margin-right: 15px;
                content: "";
                width: 25px;
                height: 25px;
                background: url(../img/accept.svg) no-repeat;
            }
        }
    }

    .slick-prev,
    .slick-next {
        background: none !important;
        top: 50%;
        &::after {
            border-top-color: #000 !important;
            border-left-color: #000 !important;
            width: 20px !important;
            height: 20px !important;
        }
    }
    .slick-track {
    }
    .slick-prev {
        left: -5% !important;
    }
    .slick-next {
        left: 100% !important;
    }
}
.news {
    background: #fdeee7;
    border-radius: 30px;
    max-width: 1200px;
    margin: 160px auto 0;
    padding: 65px 0;
    &-block {
        margin: auto;
        max-width: 620px;
        p {
            text-align: center;
        }
        &-text {
            font-weight: 600;
            font-size: 39px;
            line-height: 130%;
            color: #05000b;
            margin: 0;
        }
    }
    &-inp-block {
        max-width: 560px;
        height: 77px;
        position: relative;
        margin-top: 35px;
    }
    &-button {
        position: absolute;
        right: 12px;
        height: 53px !important;
        top: 8px;
    }
    &-input {
        width: 100%;
        border: 1px solid rgba(5, 0, 11, 0.1);
        box-sizing: border-box;
        border-radius: 4px;
        font-weight: 500;
        font-size: 16px;
        line-height: 160%;
        background: none;
        color: #a6a6a6;
        padding: 20px;
        &::placeholder {
            font-weight: 500;
            font-size: 16px;
            line-height: 160%;
            color: #a6a6a6;
        }
    }
}
@import "slick";
@import "progress";
@import "header";
@import "footer";
@import "adaptive";
