@media all and (min-width: 800px) and (max-width: 1000px) {
}
@media all and (max-width: 1000px) {
    .choose {
        &-blocks {
            width: 90%;
        }

        &-slide {
            width: 100% !important;
            margin: 0 auto;
        }
        &-icon {
            left: 20%;
        }
    }
    .button-pack {
        width: 90%;
    }
    .users-block {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        &-left {
            order: 1;
            height: 500px;
            width: 500px;
        }
    }
    .slick-arrow {
        display: none !important;
    }

    .header {
        z-index: 10;
        &-left {
            margin-left: 10%;
        }
    }
    .header-center {
        z-index: 22;
        top: -20px;
        height: 110%;
    }
    .burger {
        &-menu {
            background: #fff;
            display: grid;
            grid-template-rows: repeat(10, 60px);
            position: absolute;
            left: -100%;
            padding: 80px 0 0 90px;
            box-sizing: border-box;
            transition: all 0.5s ease-in-out;
            li {
                margin: 20px 0;
            }
            a {
                color: rgb(98, 115, 242);
                opacity: 1 !important;
                text-transform: uppercase;
            }
            .button {
                text-transform: uppercase;
            }
            > li:last-child {
            }
        }
        &-active {
            left: 0;
        }
    }
    .close {
        font-size: 50px;
        margin: 0;
        display: block;
        position: absolute;
        right: 10%;
        top: 20px;
        color: red;
        font-weight: bold;
    }
    .header-container {
        grid-template-columns: repeat(2, 1fr) !important;
    }
    .burger-click {
        display: block;
        width: 50px;
        height: 3px;
        background: #000;
        justify-self: end;
        margin-right: 50px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 50px;
            height: 3px;
            background: #000;
            position: absolute;
            top: -15px;
        }
        &::before {
            position: absolute;
            content: "";
            display: block;
            width: 50px;
            height: 3px;
            background: #000;
            top: 15px;
        }
    }
    .main-content-container {
        > div {
            width: 90%;
            margin-left: auto;
            margin-right: auto;
            img {
                width: 100%;
            }
        }
    }
}
